<template>
  <div>
    <el-table :data="data">
      <el-table-column label="序号" type="index" width="100"></el-table-column>
      <el-table-column label="姓名" prop="name">
        <template slot="header">
          优秀{{headerObj['yx']}}
        </template>
      </el-table-column>
      <el-table-column label="良好" prop="age">
        <template slot="header">
          良好{{headerObj['lh']}}
        </template>
      </el-table-column>
      <el-table-column label="一般" prop="sex">
        <template slot="header">
          一般{{headerObj['yb']}}
        </template>
      </el-table-column>
    </el-table>


    <el-button @click="modifyTable">修改</el-button>


    <slotTest>
      <div slot="headerObj">测试{{title}}</div>
    </slotTest>
  </div>
</template>

<script>
import slotTest from '@/components/slotTest'
export default {
  components: {slotTest},
  data() {
    return {
      data: [],
      headerObj: {
       yx: '',
        lh: '',
        yb: ''
      },
      title:'我是测试文字'
    }
  },
  created() {
    this.modifyTable()

  },
  methods: {
    modifyTable() {
      this.title = this.title + '1'

      return
      let obj = {
        yx: '',
        lh: '',
        yb: ''
      }

      for(let i in obj) {
        this.headerObj[i] = parseInt(Math.random() * 10 + 1) + '分'
      }
    }
  }
} 
</script>